	module.exports = {"data":{"site":{"siteMetadata":{"title":"Magento 1 Frontend Bits","author":"Claire Parker-Jones"}},"markdownRemark":{"id":"/Users/Claire/gatsby-blog/src/pages/cdata/index.md absPath of file >>> MarkdownRemark","html":"<p>In my opinion: no.</p>\n<p>But it depends.</p>\n<h2>What does CDATA do?</h2>\n<p>You may have found <code>CDATA</code> declarations in template files (filetype <code>.phtml</code>),</p>\n<p>CDATA marks the text inside as not being XML or HTML. It is ignored by the XML parser.</p>\n<p><a href=\"https://www.w3.org/TR/REC-xml/#sec-cdata-sect\">https://www.w3.org/TR/REC-xml/#sec-cdata-sect</a></p>\n<p><a href=\"https://magento.stackexchange.com/questions/164569/put-cdata-it-is-a-good-practice-or-not-necessarily-magento-1-9\">https://magento.stackexchange.com/questions/164569/put-cdata-it-is-a-good-practice-or-not-necessarily-magento-1-9</a></p>\n<p>It’s for validating XHTML markup when JavaScript is inline in the template. It means the validator doesn’t fail and ignores anything between those tags.</p>\n<p>This shouldn’t matter if you’re using the latest html5 doctype.</p>\n<p><a href=\"https://stackoverflow.com/questions/66837/when-is-a-cdata-section-necessary-within-a-script-tag?rq=1\">https://stackoverflow.com/questions/66837/when-is-a-cdata-section-necessary-within-a-script-tag?rq=1</a></p>","frontmatter":{"title":"Do you need CDATA in templates?","date":"February 20, 2018"}}},"pathContext":{"slug":"/cdata/","previous":{"fields":{"slug":"/atom-snippets/"},"frontmatter":{"title":"How to add snippets for templates in Atom"}},"next":{"fields":{"slug":"/cms-cms/"},"frontmatter":{"title":"How to output a CMS block from another CMS block"}}}}

